/* eslint-disable */

import React from 'react';
import ReactDOM from 'react-dom';
import { createForm } from 'rc-form';

class App extends React.Component {
  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log("Received values of form: ", values);
      }
    });
  };

  handleSelectChange = value => {
    console.log(value);
  };

  render() {
    const { form } = this.props;
    const { getFieldDecorator, getFieldsError } = form;
    const code = form.getFieldValue("Demo1");

    console.log('Error:', getFieldsError());

    return (
      <form onSubmit={this.handleSubmit}>
        {getFieldDecorator("Demo1", {
          rules: [{ required: true, message: "Please select your gender!" }]
        })(
          <select onChange={this.handleSelectChange}>
            <option value="-">-</option>
            <option value="err1">Error Demo 2</option>
            <option value="err2">Error Demo 3</option>
          </select>
        )}
        {getFieldDecorator("Demo2", {
          rules: [
            {
              required: code == "err1",
              message: "Please select your gender!"
            }
          ]
        })(
          <input />
        )}
        {getFieldDecorator("Demo3", {
          rules: [
            {
              required: code == "err2",
              message: "Please select your gender!"
            }
          ]
        })(
          <input />
        )}
        <button type="primary" type="submit">
          Submit
        </button>
      </form>
    );
  }
}

const WrappedApp = createForm()(App);


ReactDOM.render(
  <WrappedApp />
  , document.getElementById('__react-content'));

/* eslint-enable */



